html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto; }

ul, ul li {
  list-style-type: none;
  padding: 0;
  margin: 0; }

input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

body {
  font-size: 16px;
  font-family: "Roboto", sans-serif;
  color: #2c3e50;
  font-weight: 400;
  line-height: 1.4; }

.container {
  max-width: 1200px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto; }

a {
  color: #e67e22;
  transition: all 0.3s;
  text-decoration: none; }
  a:hover {
    color: #d35400;
    text-decoration: underline; }

.site-content {
  padding-top: 15px; }

h1, h2, h3, h4, h5, h6 {
  font-weight: 600; }

h1 {
  font-size: 50px; }

h2 {
  font-size: 34px; }

h3 {
  font-size: 30px; }

h4 {
  font-size: 26px; }

h5 {
  font-size: 22px; }

h6 {
  font-size: 18px; }

.header {
  background: #2c3e50; }
  .header__logotype {
    text-decoration: none !important;
    display: inline-block;
    font-weight: 700;
    padding: 15px 0;
    font-size: 20px;
    color: #ffffff;
    letter-spacing: 0.5px; }
  .header__inner {
    display: flex;
    justify-content: space-between;
    align-items: center; }
  .header__add a {
    background: #fff;
    width: 35px;
    height: 35px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    border-radius: 5px;
    color: #2c3e50;
    text-decoration: none !important; }
  .header__login {
    display: flex;
    color: #ffffff; }
    .header__login a {
      color: #ffffff !important; }
    .header__login span {
      margin: 0 5px; }
  .header__right {
    display: flex;
    align-items: center; }
    .header__right .logout {
      margin-right: 15px; }

.tasks {
  margin-bottom: 30px; }
  .tasks__heading {
    margin-bottom: 15px; }
    .tasks__heading .status__label {
      display: inline-block; }
  .tasks__task {
    padding: 6px 10px;
    border-radius: 5px;
    border: 1px solid rgba(226, 226, 226, 0.57); }
    .tasks__task:not(:last-of-type) {
      margin-bottom: 15px; }
    .tasks__task__heading {
      display: flex;
      align-items: flex-start;
      justify-content: space-between; }
    .tasks__task__title {
      padding: 5px; }
    .tasks__task__actions {
      display: flex; }
    .tasks__task__action {
      padding: 3px;
      width: 60px;
      display: inline-block;
      text-align: center;
      color: #2c3e50 !important;
      border: 1px solid #2c3e50;
      text-decoration: none !important;
      font-size: 14px; }
      .tasks__task__action:first-of-type {
        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px; }
      .tasks__task__action:last-of-type {
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;
        border-left-width: 0; }
      .tasks__task__action:not(:first-of-type):not(:last-of-type) {
        border-left-width: 0; }
      .tasks__task__action:hover {
        color: #fff !important;
        background: #2c3e50; }

.view__actions, .edit__actions {
  margin-bottom: 15px; }

.status__label {
  padding: 6px;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 600;
  border-radius: 5px;
  min-width: 130px;
  text-align: center;
  margin-right: 10px; }
  .status__label.open {
    background: #e2e2e2; }
  .status__label.in_progress {
    background: #efda9b; }
  .status__label.done {
    background: #9befb1; }

input[type="text"],
input[type="number"],
input[type="email"],
input[type="password"],
textarea, select {
  font-size: 16px;
  padding: 10px;
  border: 1px solid #2c3e50;
  border-radius: 5px;
  width: 100%;
  box-sizing: border-box; }

#acf-form input[type="text"],
#acf-form input[type="number"],
#acf-form input[type="email"],
#acf-form input[type="password"],
#acf-form textarea, #acf-form select {
  font-size: 16px;
  padding: 10px;
  border: 1px solid #2c3e50;
  border-radius: 5px;
  width: 100%;
  box-sizing: border-box; }

input[type="submit"], input[type="button"], button, .btn {
  border: 1px solid #2c3e50;
  background: transparent;
  color: #2c3e50 !important;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s;
  text-decoration: none !important;
  display: inline-block; }
  input[type="submit"]:hover, input[type="button"]:hover, button:hover, .btn:hover {
    color: #fff !important;
    background: #2c3e50; }

.field__group {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  align-items: flex-start;
  position: relative; }
  .field__group > label, .field__group > .label {
    font-size: 14px;
    font-weight: bold;
    margin-bottom: 5px; }

.field__submit {
  display: flex;
  justify-content: flex-end; }

.acf-form-submit {
  padding: 0 12px;
  display: flex;
  justify-content: flex-end; }

.acf-fields > .acf-field:first-child {
  padding-top: 0; }

.auth-form {
  max-width: 400px; }

.error {
  color: #8b0000;
  font-size: 14px;
  padding-top: 2px;
  position: absolute;
  bottom: -19px; }
